import { useState } from "react";
import { Eye, EyeOff, Lock, ArrowRight, MoonStar, Shield } from "lucide-react";

export default function ResetPassword() {
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [passwordStrength, setPasswordStrength] = useState(0);
  
  const calculateStrength = (pass) => {
    if (!pass) return 0;
    
    let strength = 0;
    
    // Base points for length
    if (pass.length >= 6) strength += 1;
    if (pass.length >= 8) strength += 1;
    if (pass.length >= 10) strength += 1;
    
    // Diversity points
    if (/[A-Z]/.test(pass)) strength += 1;
    if (/[0-9]/.test(pass)) strength += 1;
    if (/[!@#$%^&*]/.test(pass)) strength += 1;
    
    return Math.min(5, strength);
  };
  
  const getStrengthLabel = () => {
    const labels = ["", "ضعیف", "متوسط", "خوب", "قوی", "عالی"];
    return labels[passwordStrength];
  };
  
  const getStrengthColor = () => {
    const colors = ["bg-gray-200", "bg-red-500", "bg-orange-500", "bg-yellow-500", "bg-blue-500", "bg-green-500"];
    return colors[passwordStrength];
  };

  const handlePasswordChange = (e) => {
    const newPassword = e.target.value;
    setPassword(newPassword);
    setPasswordStrength(calculateStrength(newPassword));
  };

  const validateForm = () => {
    if (password.length < 6) {
      return false;
    }
    
    if (password !== confirmPassword) {
      return false;
    }
    
    return true;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (validateForm()) {
      setIsSubmitted(true);
    }
  };

  const toggleShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const toggleShowConfirmPassword = () => {
    setShowConfirmPassword(!showConfirmPassword);
  };

  if (isSubmitted) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gradient-to-br from-blue-50 to-indigo-50 font-sans" dir="rtl">
        <div className="w-full max-w-md p-8 space-y-8 bg-white rounded-2xl shadow-xl">
          <div className="text-center">
            <div className="mx-auto h-20 w-20 bg-green-100 rounded-full flex items-center justify-center">
              <Shield className="h-10 w-10 text-green-600" />
            </div>
            <h2 className="mt-6 text-2xl font-bold text-gray-900">رمز عبور با موفقیت بازنشانی شد</h2>
            <p className="mt-2 text-gray-600">رمز عبور شما با موفقیت تغییر کرده است.</p>
          </div>
          <div className="mt-6">
            <button 
              className="group relative w-full flex justify-center py-3 px-4 border border-transparent text-sm font-medium rounded-xl text-white bg-indigo-600 hover:bg-indigo-700 transition-all duration-200 shadow-md hover:shadow-lg"
              onClick={() => window.location.href = "/login"}
            >
              <span>بازگشت به صفحه ورود</span>
              <ArrowRight className="mr-2 h-4 w-4" />
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="flex items-center justify-center min-h-screen bg-gradient-to-br from-blue-50 to-indigo-50" dir="rtl">
      <div className="w-full max-w-md p-8 space-y-6 bg-white rounded-2xl shadow-xl">
        <div className="text-center">
          <div className="mx-auto h-16 w-16 bg-indigo-100 rounded-full flex items-center justify-center">
            <MoonStar className="h-8 w-8 text-indigo-600" />
          </div>
          <h2 className="mt-4 text-2xl font-bold text-gray-900">بازنشانی رمز عبور</h2>
          <p className="mt-2 text-sm text-gray-600">
            رمز عبور جدید خود را وارد کنید
          </p>
        </div>
        
        <form className="mt-6 space-y-6" onSubmit={handleSubmit}>
          <div className="space-y-5">
            <div>
              <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-1">
                رمز عبور جدید
              </label>
              <div className="relative">
                <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                  <Lock className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  id="password"
                  name="password"
                  type={showPassword ? "text" : "password"}
                  value={password}
                  onChange={handlePasswordChange}
                  className="appearance-none block w-full pr-10 pl-10 py-3 border border-gray-300 rounded-xl shadow-sm placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 text-sm"
                  placeholder="رمز عبور جدید را وارد کنید"
                  dir="rtl"
                />
                <button
                  type="button"
                  className="absolute inset-y-0 left-0 pl-3 flex items-center"
                  onClick={toggleShowPassword}
                >
                  {showPassword ? (
                    <EyeOff className="h-5 w-5 text-gray-400" />
                  ) : (
                    <Eye className="h-5 w-5 text-gray-400" />
                  )}
                </button>
              </div>
              
              {password && (
                <div className="mt-3">
                  <div className="flex items-center justify-between mb-1">
                    <span className="text-xs font-medium text-gray-700">قدرت رمز عبور:</span>
                    <span className={`text-xs font-medium ${
                      passwordStrength <= 1 ? "text-red-500" : 
                      passwordStrength === 2 ? "text-orange-500" :
                      passwordStrength === 3 ? "text-yellow-500" :
                      passwordStrength === 4 ? "text-blue-500" : "text-green-500"
                    }`}>{getStrengthLabel()}</span>
                  </div>
                  <div className="h-2 w-full bg-gray-200 rounded-full overflow-hidden">
                    <div 
                      className={`h-full ${getStrengthColor()} transition-all duration-300 ease-in-out`}
                      style={{ width: `${passwordStrength * 20}%` }}
                    ></div>
                  </div>
                </div>
              )}
            </div>
            
            <div>
              <label htmlFor="confirm-password" className="block text-sm font-medium text-gray-700 mb-1">
                تأیید رمز عبور
              </label>
              <div className="relative">
                <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                  <Lock className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  id="confirm-password"
                  name="confirm-password"
                  type={showConfirmPassword ? "text" : "password"}
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  className={`appearance-none block w-full pr-10 pl-10 py-3 border ${
                    confirmPassword && password !== confirmPassword
                      ? "border-red-300 focus:ring-red-500 focus:border-red-500"
                      : "border-gray-300 focus:ring-indigo-500 focus:border-indigo-500"
                  } rounded-xl shadow-sm placeholder-gray-400 focus:outline-none focus:ring-2 text-sm`}
                  placeholder="رمز عبور را مجدداً وارد کنید"
                  dir="rtl"
                />
                <button
                  type="button"
                  className="absolute inset-y-0 left-0 pl-3 flex items-center"
                  onClick={toggleShowConfirmPassword}
                >
                  {showConfirmPassword ? (
                    <EyeOff className="h-5 w-5 text-gray-400" />
                  ) : (
                    <Eye className="h-5 w-5 text-gray-400" />
                  )}
                </button>
              </div>
              {confirmPassword && password !== confirmPassword && (
                <p className="mt-1 text-sm text-red-600">رمز عبور مطابقت ندارد</p>
              )}
            </div>
          </div>

          <div className="text-xs text-gray-500 bg-gray-50 p-3 rounded-lg">
            <p>توصیه می‌شود رمز عبوری انتخاب کنید که:</p>
            <ul className="mt-1 space-y-1 list-disc list-inside">
              <li>حداقل ۶ کاراکتر باشد</li>
              <li>ترکیبی از حروف، اعداد و نمادها باشد</li>
              <li>از اطلاعات شخصی قابل حدس استفاده نکنید</li>
            </ul>
          </div>

          <div>
            <button
              type="submit"
              disabled={!validateForm()}
              className={`relative w-full flex justify-center py-3 px-4 border border-transparent text-sm font-medium rounded-xl text-white ${
                validateForm()
                  ? "bg-indigo-600 hover:bg-indigo-700 shadow-md hover:shadow-lg"
                  : "bg-gray-400 cursor-not-allowed"
              } transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500`}
            >
              ثبت رمز عبور جدید
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}